.infinity-loader {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 130px;
  height: 70px;
}
  
.infinity-loader .bg div,
.infinity-loader > .fg > div > div {
  width: 70px;
  height: 70px;
  border: 10px solid #462865;;
  box-sizing: border-box;
  border-radius: 50%;
  position: absolute;
}
  
.infinity-loader .right-bg {
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
  left: -10px;
}
  
.infinity-loader > .fg > div > div {
  border-color: #fdd51c #fdd51c transparent transparent;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
  position: static;
}
  
.infinity-loader > .fg > div {
  clip: rect(0, 70px, 35px, 0);
  position: absolute;
}
  
.infinity-loader > .fg > .bottom-right-rect {
  left: -10px;
  -webkit-transform: translateX(100%) scale(1, -1);
          transform: translateX(100%) scale(1, -1);
}
  
.infinity-loader > .fg > .bottom-right-rect > div {
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
}
  
.infinity-loader > .fg > .top-right-rect {
  left: -10px;
  -webkit-transform: translateX(100%) scale(-1, 1);
          transform: translateX(100%) scale(-1, 1);
}
  
.infinity-loader > .fg > .top-right-rect > div {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
  
.infinity-loader > .fg > .bottom-left-rect {
  -webkit-transform: scale(-1);
          transform: scale(-1);
}
  
.infinity-loader > .fg > .bottom-left-rect > div {
  -webkit-animation-delay: 0.75s;
          animation-delay: 0.75s;
}
  
.infinity-loader > .fg {
  -webkit-filter: drop-shadow(0 0 6px #fdd51c);
          filter: drop-shadow(0 0 6px #fdd51c);
}
  
@-webkit-keyframes spin {
  50%,
  100% {
    -webkit-transform: rotate(495deg);
            transform: rotate(495deg);
  }
}
  
@keyframes spin {
  50%,
  100% {
    -webkit-transform: rotate(495deg);
            transform: rotate(495deg);
  }
}  
